import "core-js/modules/es.array.push.js";
import { ref, onMounted } from "vue";
import { useRouter } from "vue-router";
import axios from "axios";
import jpg1 from "@/assets/image/home1.jpg";
import jpg2 from "@/assets/image/home2.jpg";
import jpg3 from "@/assets/image/home3.jpg";
import jwtDecode from "jwt-decode";
export default {
  setup() {
    const images = [jpg1, jpg2, jpg3];
    const value = ref("");
    const reValue = ref("");
    const show = ref(false);
    const show_1 = ref(false);
    const show_2 = ref(false);
    const show_3 = ref(false);
    const show_4 = ref(false);
    const checked = ref();
    const router = useRouter();
    const currentTime = ref(["12", "00"]);
    const userId = ref("");
    const remindSwitch = ref("");
    const token = localStorage.getItem("jwtToken"); // localStorageJWT
    const noticeMessages = ref("");
    var y = window.innerHeight * 0.74;
    var x = window.innerWidth * 0.8;
    const offset_produce = ref({
      x: x,
      y: y
    });
    const message = ref("");
    if (!token) {
      router.replace("/login");
    }
    const headers = {
      Authorization: `Bearer ${token}`
    };
    onMounted(() => {
      show_1.value = true;
      fetchLoginToken();
    });
    const fetchLoginToken = () => {
      axios.post("/api/loginToken?token=" + token).then(response => {
        if (response.data.code == 0) {
          console.error(response.data.data);
          router.replace("/login");
          return;
        }
        console.log(response.data.data);
        const decodedToken = jwtDecode(token);
        // 
        userId.value = decodedToken.id; // ID
        fetchDataAndFillForm();
        // 
        // 
      }).catch(error => {
        console.error("loginToken", error);
        router.replace("/login");
      });
      show_1.value = false;
    };
    const fetchDataAndFillForm = () => {
      axios.get("/api/findAllBroadcast", {
        headers
      }).then(response => {
        //  userList.value 
        noticeMessages.value = response.data.data;
        show_2.value = false;
      }).catch(error => {
        console.error("Failed to fetch data:", error);
      });
    };
    const onClick = () => {
      show_2.value = true;
    };
    const checkConfirm = () => {
      const broadCast = {
        broadCastText: value.value
      };
      fetch("/api/addBroadcast", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Authorization: `Bearer ${token}`
        },
        body: JSON.stringify(broadCast)
      }).then(response => response.json()) //  .json() 
      .then(data => {
        if (data.code == 1) {
          console.log("", data.data);
          const broadCastId = data.data;
          broadCast.broadCastId = broadCastId;
          noticeMessages.value.push(broadCast);
          message.value = "";
        } else {
          message.value = "";
        }
      }).catch(error => {
        message.value = "";
        console.log(error);
      });
      show_3.value = true; // 
      // 
      setTimeout(() => {
        show_3.value = false; // 
      }, 1000); // 20002
      value.value = "";
    };
    const checkCancel = () => {
      return;
    };
    const onClickLeft = () => router.replace("/setting");
    let item_tem = "";
    const requireItem = item => {
      show_4.value = true;
      item_tem = item;
      return;
    };
    const reCheckConfirm = () => {
      const broadCast = {
        broadCastId: item_tem.broadCastId,
        broadCastText: reValue.value
      };
      console.log(item_tem.broadCastId);
      fetch("/api/requireBroadcast", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Authorization: `Bearer ${token}`
        },
        body: JSON.stringify(broadCast)
      }).then(response => response.json()) //  .json() 
      .then(data => {
        if (data.code == 1) {
          console.log("", data.data);
          item_tem.broadCastText = broadCast.broadCastText;
          message.value = "";
        } else {
          message.value = "";
        }
      }).catch(error => {
        message.value = "";
        console.log(error);
      });
      show_3.value = true; // 
      // 
      setTimeout(() => {
        show_3.value = false; // 
      }, 1000); // 20002
      reValue.value = "";
    };
    const reCheckCancel = () => {
      return;
    };
    const deleteItem = (broadCastId, position) => {
      axios.get(`/api/deleteBroadcast?id=${broadCastId}`, {
        headers
      }).then(response => {
        //  userList.value 
        message.value = response.data.data;
        console.log(response.data);
        noticeMessages.value.splice(position, 1); // 
      }).catch(error => {
        console.error("Failed to fetch data:", error);
        message.value = "";
      });
      show_3.value = true; // 
      // 
      setTimeout(() => {
        show_3.value = false; // 
      }, 1000); // 20002
    };

    return {
      images,
      value,
      show,
      show_1,
      show_2,
      show_3,
      show_4,
      checked,
      currentTime,
      remindSwitch,
      onClickLeft,
      noticeMessages,
      offset_produce,
      onClick,
      checkConfirm,
      checkCancel,
      requireItem,
      reValue,
      reCheckConfirm,
      reCheckCancel,
      deleteItem,
      message
    };
  }
};